.organisms-navigation-style1 {
	position: fixed;
	width: 100%;
	left: 0;
	top: 0;
	z-index: 3;
	.calendar-btn-movil {
		background: #7fbbce;
		padding: 5px 20px;
		border-radius: 5px;
		display: none;
		margin-left: 30px;
		width: 116px;
		font-size: 14px;
		color: #314554!important;
		@media screen and (max-width: $atom-screen-xs-min) {
			display: inline-block;
		}
	}
	>.container-fluid {
		padding: 20px 50px;
		@media screen and (max-width: $atom-screen-xs-min) {
			padding: 20px;
		}
		.form {
			.btn {
				margin-left: 15px;
			}
			.input {
				&:nth-child(1) {
					margin-right: 10px;
				}
			}
		}
		.logo {
			width: 60px;
			@media screen and (max-width: 480px) {
				width: 50px;
			}
		}
	}
	ul {
		li {
			margin: 0 10px;
			a {
				&:hover {
					color: white;
				}
			}
			.calendar-btn {
				background: #7fbbce;
				padding: 5px 20px;
				border-radius: 5px;
				@media screen and (max-width: $atom-screen-xs-min) {
					display: none;
				}
			}
		}
	}
	@media screen and (max-width: 480px) {
		ul {
			display: none;
		}
	}
	.dropdown-toggle::before {
		display: none;
	}
	.dropdown-menu {
		@media screen and (max-width: $atom-screen-xs-min) {
			top: 50px!important;
			right: 0;
			left: auto!important;
			transform: inherit!important;
			box-shadow: 0 1px 8px rgba(0,0,0,0.3);
			padding: 20px 15px!important;
			li {
				margin-bottom: 10px;
				&:last-child {
					margin-bottom: 0;
				}
				a {
					padding: 0;
				}
			}
		}
	}
	.organisms-navigation-menu {
		@media screen and (max-width: $atom-screen-xs-min) {
			margin-top: 12px;
		}
	}
}

.organisms-navigation-style1-active {
	box-shadow: 0 1px 8px rgba(0,0,0,.3);
	.container-fluid {
		padding: 10px 50px;
		@media screen and (max-width: $atom-screen-xs-min) {
			padding: 10px 20px;
		}
		.logo {
			width: 30px;
		}
		ul {
			padding-top: 4px;
		}
	}
	.organisms-navigation-menu {
		@media screen and (max-width: $atom-screen-xs-min) {
			margin-top: 3px;
		}
	}
}

.organisms-navigation-style2 {
	.logo {
		width: 140px;
	}
	.top-nav {
		height: 40px;
		.list {
			padding-top: 6px;
			li {
				margin: 0 10px;
			}
		}
	}
	.footer-nav {
		padding-top: 20px;
		text-align: center;
		.list {
			padding: 20px 0;
			margin-top: 10px;
			li {
				margin: 0 10px;
			}
		}
	}
}

.organisms-navigation-style2-active {
	.logo {
		width: 100px;
		display: inline-block;
		margin-right: 50px;
	}
	.top-nav {
		height: 40px;
		position: fixed;
		z-index: 3;
		width: 100%;
		left: 0;
		top: 0;
		.list {
			li {
				margin: 0 10px;
			}
		}
	}
	.footer-nav {
		padding-top: 10px;
		padding-bottom: 10px;
		text-align: left;
		position: fixed;
		z-index: 3;
		width: 100%;
		top: 40px;
		left: 0;
		.list {
			padding-top: 0;
			margin-top: 0;
			float: right;
			padding-bottom: 0;
			border-top: none;
			display: inline-block;
			border-top: none;
		}
	}
}

.organisms-navigation-style3 {
	position: fixed;
	width: 100%;
	z-index: 3;
	.top-nav {
		height: 50px;
		padding-left: 15px;
		.logo {
			padding-top: 5px;
			width: 100px;
			display: inline-block;
			margin-right: 50px;
		}
		.list {
			padding-top: 10px;
			li {
				margin: 0 10px;
			}
		}
	}
	.footer-nav {
		padding-top: 20px;
		padding-bottom: 20px;
		padding-left: 15px;
		text-align: left;
		z-index: 3;
		width: 100%;
		top: 40px;
		left: 0;
		.list {
			padding-top: 0;
			margin-top: 0;
			padding-bottom: 0;
			border-top: none;
			margin: 0 10px;
			display: inline-block;
			li {
				margin: 0 10px;
				&:nth-child(1) {
					margin-left: 0;
				}
			}

		}
	}
}

.organisms-navigation-style3-content {
	top: 113px;
}

.organisms-navigation-style4 {
	position: fixed;
	height: 100vh;
	.top-nav {
		.logo {
			width: 100%;
			padding-left: 10px;
			padding-right: 10px;
			margin-top: 20px;
			margin-bottom: 50px;
		}
	}
	.sections {
		li {
			margin: 10px 0;
		}
	}
	.footer-nav {
		position: absolute;
		bottom: 0;
		li {
			margin: 3px 0;
		}
		p {
			margin-top: 10px;
		}
	}
}

.organisms-navigation-style4-content {
	float: right;
	margin-left: 16.66667%;
}

