				}
			}
		}
	}
	@media screen and (max-width: 480px) {
		ul {
			display: none;
		}
	}
}

.organisms-navigation-style5-active {
	.container {
		padding: 10px 0;
		.logo {
			width: 100px;
		}
		ul {
			padding-top: 7px;
		}
	}

}

