.organisms-navigation-style6-modal {
	.modal-dialog {
		margin: 0;
		width: 100%;
		max-width: 100%;
		.modal-content {
			width: 100%;
			height: 100vh;
			background: rgba(0,0,0,0.8);
			border-radius: 0;
			border: 0;
			box-shadow: none;
		}		
	}

	.modal-header {
		border-bottom: none;
		.close {
			opacity: 0.8;
			margin-top: 30px;
			margin-right: 30px;
			img {
				width: 50px;
			}
		}
	}

	.modal-body {
		.sections {
			margin-top: 80px;
			li {
				margin-bottom: 20px;
			}
		}
	}
}