.organisms-footer-style1 {
	padding: 50px 0 20px 0;
	.logo {
		width: 120px;
		margin-bottom: 40px;
	}
	.sections {
		margin-bottom: 40px;
		li {
			padding: 0 15px;
		}
	}
	.copyright {
		padding-top: 40px;
	}
}

.organisms-footer-style2 {
	padding: 50px 0 20px 0;
	.logo {
		width: 230px;
	}

	.information {
		margin-top: 15px;
	}

	.sections {
		margin-top: 40px;
		li {
			padding: 0 15px;
		}
	}

	.copyright {
		padding-top: 30px;
		margin-top: 30px;
	}

}

.organisms-footer-style3 {
	padding: 50px 0 20px 0;
	.content-logo {
		width: 50%;
		float: right;
		.logo {
			width: 100%;
		}
		.information {
			margin-top: 0;
		}
	}

	.information {
		margin-top: 15px;
	}

	.sections {
		display: inline-block;
		vertical-align: top;
		li {
			padding: 0 15px;
			&:nth-child(1) {
				margin-bottom: 5px;
			}
		}
	}

	.social-media {
		ul {
			li {
				margin: 0 5px;
				img {
					width: 35px;
				}
			}
		}
	}

	.copyright {
		padding-top: 30px;
		margin-top: 30px;
		border-top: solid 1px rgba(0,0,0,0.6);
		p {
			display: inline-block;
			&:nth-child(2) {
				float: right;
			}
		}
	}

}

